body{
    width: 1000px;
}
#hero-list{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

#hero-list > li{
    display: flex;
    width: 80px;
    background-color: gray;
    font-size: 20;
    margin: 2px;
    border: 3px solid lightblue;
    border-radius: 10px;
    flex-direction: column;
    align-items: center;
}

.role{
    background-color: darkgray;
    color: white;
    padding: 1px auto;
    max-width: 100%;
}

.name{
    max-width: 90%;
    overflow: hidden;
    white-space: nowrap;
}

.portrait{
    width: 100%;
}

